.data-card {
  overflow: hidden;
  width: auto;
  height: 650px;
  border-radius: 30px;
  text-decoration: none;
  background: rgb(255, 255, 255);
  margin: 1em;
  padding: 2.75em 2em;
  box-shadow: 0 1.5em 2.5em 0.5em rgba(0, 0, 0, 0.1);
  transition: background 0.45s ease, -webkit-transform 0.45s ease;
  transition: transform 0.45s ease, background 0.45s ease;
  transition: transform 0.45s ease, background 0.45s ease, -webkit-transform 0.45s ease;
}
.data-card .card-title {
  color: #627084;
  font-size: 38px;
  font-weight: 600;
  line-height: 1;
  padding-bottom: 0.5em;
  margin: 0 0 0.142857143em;
  border-bottom: 2px solid #92a9bd;
  transition: color 0.45s ease, border 0.45s ease;
}
.data-card .card-subtitle {
  color: #627084;
  text-transform: uppercase;
  font-size: 1.125em;
  font-weight: 700;
  line-height: 1;
  letter-spacing: 0.1em;
  margin: 0 0 1.777777778em;
  transition: color 0.45s ease;
}
.data-card .card-item {
  font-size: 20px;
  color: rgb(0, 0, 0);
  font-weight: 600;
  line-height: 1.8;
  margin-top: 60px;
  -webkit-transform: translateY(-1em);
          transform: translateY(-1em);
  transition: opacity 0.45s ease, -webkit-transform 0.5s ease;
  transition: opacity 0.45s ease, transform 0.5s ease;
  transition: opacity 0.45s ease, transform 0.5s ease, -webkit-transform 0.5s ease;
}

.data-card:hover {
  background: #7c99ac;
  -webkit-transform: scale(1.02);
          transform: scale(1.02);
}
.data-card:hover .card-title {
  color: #fff;
  border-bottom-color: #2c76a7;
}
.data-card:hover .card-subtitle {
  color: #fff;
}
.data-card:hover .card-item {
  opacity: 1;
  -webkit-transform: none;
          transform: none;
  color: #fff;
}
.data-card:hover .link-text {
  color: #fff;
}

@-webkit-keyframes point {
  0% {
    -webkit-transform: translateX(0);
            transform: translateX(0);
  }
  100% {
    -webkit-transform: translateX(0.125em);
            transform: translateX(0.125em);
  }
}

@keyframes point {
  0% {
    -webkit-transform: translateX(0);
            transform: translateX(0);
  }
  100% {
    -webkit-transform: translateX(0.125em);
            transform: translateX(0.125em);
  }
}

